.input{background:#fff;border:1px solid #E0E0E0;border-radius:6px;box-shadow:none;margin-bottom:12px;color:#272727;text-transform:capitalize}.input::-webkit-input-placeholder{font-weight:300}.input::placeholder{font-weight:300}.input:focus{outline:none;box-shadow:none;border:1px solid #FEB72E}.select{margin-bottom:12px}.btn{background:#FEB72E;border:1px solid #FEB72E;color:#fff;min-width:100%;padding:0.5em 1e.5m}body{font-family:"Poppins",sans-serif;background:#f9f9f9;color:#272727;font-weight:300}body .title{font-size:1.4em}body .title.main{font-weight:700;font-size:2.4em}body .edit-btn{cursor:pointer}footer{padding:2rem 0}footer p{margin:0}

.weekPicker__week{display:flex;justify-content:space-evenly;flex-wrap:wrap;padding:1.5rem 0;width:100%}.weekpicker__week--day{margin:0.5rem;padding:0.3rem 0.6rem;border-radius:100px;-webkit-border-radius:100px;-moz-border-radius:100px;-ms-border-radius:100px;-o-border-radius:100px;cursor:pointer}.weekpicker__week--day:hover{background-color:#f2f2f2}.weekpicker__week--day.selected{background-color:#FEB72E;color:#fff}@media screen and (max-width: 767px){.weekpicker__week--day{padding:0.3rem;width:3.7ch;overflow:hidden;white-space:nowrap}}

.scheduler__Container{max-width:100%;overflow-x:scroll}.scheduler__Container .scheduler__day--column{min-width:20%;width:100px}.scheduler__Container .scheduler__day--column .scheduler__day--header{font-size:12px;text-align:center;color:#4B4B4B;padding:8px;border-bottom:1px solid #c4c4c4}.scheduler__Container .scheduler__day--column .scheduler__day--header h6,.scheduler__Container .scheduler__day--column .scheduler__day--header h5{margin:0}.scheduler__Container .scheduler__day--column .scheduler__day--header h6{font-size:12px;font-weight:300;color:#8E8E8E}.scheduler__Container .scheduler__day--column .scheduler__day--header h5{font-size:15px}.scheduler__Container .scheduler__cell{cursor:pointer;background:#fff;min-height:35px;color:#717171;font-weight:500;padding:8px;text-align:center;font-size:11px;border-right-width:thin;border-right-style:solid;border-right-color:#c4c4c4;border-bottom-width:thin;border-bottom-style:dashed;border-bottom-color:#7fc65c7a}.scheduler__Container .scheduler__cell.selected{background:#7FC65C;color:#fff;border-color:#88e659}.scheduler__Container .cell-absolute{position:absolute;width:100%;top:0;left:0;right:0;bottom:0;background:transparent}.scheduler__Container .cell-absolute.selected{background:rgba(127,198,92,0.6);background-blend-mode:multiply}.scheduler__Container .cell-relative{position:relative;width:100%;top:0;left:0;right:0;bottom:0}.scheduler__Container .cell-relative.selected{background:rgba(127,198,92,0.6);background-blend-mode:multiply}

